import React, { FC, useEffect, useState } from 'react';
import { useForm } from 'react-hook-form';
import { useSelector } from 'react-redux';
import { RouteComponentProps } from 'react-router';
import { Card, CardBody, CardTitle, Col, CustomInput, Row } from 'reactstrap';
import {
  ERROR,
  InputGroupInline,
  InputTooltipProps,
  InputType,
  REFERENCE,
  showToast,
  WindowComponent,
  WindowComponentProps,
  withTooltip,
} from 'summer';
import { hasEmpresa } from '../../../../../../dependencies/empresaDependency';
import { hasEmpresaMCG } from '../../../../../../dependencies/empresaMCGDependency';
import { getDefaultReportBasicValues } from '../../../../../../models/dtos/ger/reportBasicValues';
import { getDefaultReportViewOptions, ReportViewOptions } from '../../../../../../models/dtos/ger/reportViewOptions';
import { LivroFolha } from '../../../../../../models/dtos/mcg/livroFolha';
import { NORMAL } from '../../../../../../models/enumerated/ger/consolidarEnum';
import { CONSOLIDAR_ENUM_VALUES } from '../../../../../../models/enumerated/mcg/consolidarEnum';
import { buscarConfigLivroFolhaMCG } from '../../../../../../services/mcg.service';
import { Reducers } from '../../../../../../store/ducks';
import { GlobalState } from '../../../../../../store/ducks/global.duck';
import ResponsavelIS from '../../../../../components/ger/responsavelIS/ResponsavelIS';
import CustomizacaoTermoIS from '../../../../../components/mcg/customizacaoTermoIS/CustomizacaoTermoIS';
import ReportForm from '../../../../../components/report/ReportForm';
import { CLASS_KEY, DemonstracaoResultadoRV, REPORT_EVENT, REPORT_TITLE } from '../demonstracaoResultadoRV';

const ADJUST_TITTLE = REPORT_TITLE + ' Anual';

const DEFAULT_VALUES: DemonstracaoResultadoRV = {
  ...getDefaultReportBasicValues(ADJUST_TITTLE),
  acumularDRE: true,
  consolidacao: NORMAL.value,
  exibirFormatacaoValor: true,
  gerarCabecalhoPadraoAdministradora: false,
  imprimirInformacaoDiario: true,
  outrosResponsaveis: [],
  representante: null,
  termo: null,
  termo2: null,
  virtualization: true,
};

const DEFAULT_OPTIONS: ReportViewOptions = {
  ...getDefaultReportViewOptions(REPORT_EVENT, REFERENCE.value),
  exibirConfiguracaoDadosEmpresa: false,
  exibirSegmentacaoFolha: true,
};

const maxWidth: number = 250;
const maxWidthTermo: number = 200;

const DemonstracaoResultadoAnualReportView: FC<WindowComponentProps & RouteComponentProps> = props => {
  const globalReducer = useSelector<Reducers, GlobalState>(state => state.globalReducer);
  const { handleSubmit, ...formProps } = useForm<any>({ defaultValues: DEFAULT_VALUES, mode: 'onBlur' });
  const [isEnabledIsMatriz, setIsEnabledIsMatriz] = useState(false);
  const [isEnabledTermoTwo, setIsEnabledTermoTwo] = useState(false);
  const [isEnabledTermoOne, setIsEnabledTermoOne] = useState(true);
  const [isEnabledInfoDiario, setIsEnabledInfoDiario] = useState(true);

  const carregarParametroEmpresa = () => {
    if (globalReducer.globalParameter.empresa.matriz === null) {
      setIsEnabledIsMatriz(true);
      formProps.getValues().matrizFilial = globalReducer.globalParameter.empresa.centralizar;
    }
  };

  const getInfoDiario = () => {
    const {
      empresa: { id },
    } = globalReducer.globalParameter;

    buscarConfigLivroFolhaMCG(id, {
      errorFunction: mensagem => {
        showToast(mensagem, ERROR);
      },
      thenFunction: (result: LivroFolha) => {
        setTimeout(() => {
          formProps.setValue('livroDiario', result.diarioLivro, true);
          formProps.setValue('referenciaInicial', globalReducer.globalParameter.empresaMCG.refAtual, true);
        }, 700);
      },
    });
  };

  useEffect(
    () => {
      if (globalReducer.globalParameter.empresa) {
        getInfoDiario();
        carregarParametroEmpresa();
      }
    }, // eslint-disable-next-line react-hooks/exhaustive-deps
    []
  );

  const enableTermoTwo = () => {
    if (formProps.getValues().isImprimirTermosSeparados && !formProps.getValues().modoSimplificado) {
      setIsEnabledTermoTwo(true);
    } else {
      setIsEnabledTermoTwo(false);
      formProps.unregister(['termo2']);
    }
  };

  const enableTermo = () => {
    if (formProps.getValues().modoSimplificado) {
      formProps.setValue('isImprimirTermosSeparados', false, true);
      setIsEnabledTermoTwo(false);
      formProps.unregister(['termo2']);
      setIsEnabledTermoOne(false);
      formProps.unregister(['termo']);
    } else {
      setIsEnabledTermoOne(true);
    }
  };

  const enableInfoDiario = () => {
    if (formProps.getValues().imprimirInformacaoDiario) {
      setIsEnabledInfoDiario(true);
    } else {
      setIsEnabledInfoDiario(false);
    }
  };

  const validateExtras = () => {
    const erros = [];
    const values = formProps.getValues();

    if (values.imprimirInformacaoDiario && values.folhaInicial > values.folhaFinal) {
      erros.push('A página inicial do Livro Diário deve ser menor que a página final.');
    }

    if (values.termo1 != null && values.termo2 != null) {
      if (values.termo1.id === values.termo2.id) {
        erros.push('O Termo 1 selecionado é igual ao Termo 2.');
      }
    }

    return erros;
  };

  const getDescricaoReceitaOperacionalPadrao = () => {
    formProps.setValue('descricaoReceitaOperacional', '= RECEITA OPERACIONAL LÍQUIDA', true);
  };

  const getDescricaoLucroBrutoPadrao = () => {
    formProps.setValue('descricaoLucroBruto', '= LUCRO BRUTO', true);
  };

  const getDescricaoResultadoOperacionalPadrao = () => {
    formProps.setValue('descricaoResultadoOperacional', '= RESULTADO OPERACIONAL LÍQUIDO', true);
  };

  const getDescricaoResultadoExercicioPadrao = () => {
    formProps.setValue('descricaoResultadoExercicio', '= RESULTADO DO EXERCÍCIO', true);
  };

  const getDescricaoCSLLPadrao = () => {
    formProps.setValue('descricaoCSLL', '= RESULTADO ANTES DA C.S.L.L.', true);
  };

  const getDescricaoImpostoRendaPadrao = () => {
    formProps.setValue('descricaoImpostoRenda', '= RESULTADO ANTES DO IMPOSTO DE RENDA', true);
  };

  const propToolTip = (messagem: string): InputTooltipProps => {
    return {
      message: messagem,
      placement: 'top-start',
    };
  };

  return (
    <WindowComponent
      title="Demonstração do Resultado Anual"
      useGlobalParameter={true}
      globalParameter={globalReducer.globalParameter}
      dependencies={[() => hasEmpresa(globalReducer.globalParameter), () => hasEmpresaMCG(globalReducer.globalParameter)]}
      wikiId={1215}
      wikiTitle={'Demonstração de Resultado'}
    >
      <Row>
        <Col>
          <div className="p-3 mb-2 bg-light text-dark text-center font-weight-bolder">
            Atenção: Ao Gerar o Relatório de Demonstração de Resultado Anual será aberto o encerramento de contas de resultado no processo de geração.
            Ao final do processo ocorrerá novamente o fechamento da forma que estava antes da geração do relatório.
          </div>
        </Col>
      </Row>
      <Row>
        <Col>
          <CustomInput
            type="switch"
            onChange={enableTermo}
            label="Modo Simplificado"
            name="modoSimplificado"
            id="modoSimplificado"
            innerRef={formProps.register}
          />
        </Col>
        <Col>
          <CustomInput
            type="switch"
            label="Comparar com o Período Anterior"
            name="compararPeriodoAnterior"
            id="compararPeriodoAnterior"
            innerRef={formProps.register}
          />
        </Col>
      </Row>

      <Row>
        <Col>
          <CustomInput
            type="switch"
            label="Imprimir Informação do Diário"
            name="imprimirInformacaoDiario"
            onChange={enableInfoDiario}
            id="imprimirInformacaoDiario"
            innerRef={formProps.register}
          />
        </Col>
        <Col>
          <CustomInput
            type="switch"
            label="Imprimir Termo em Página Distinta"
            name="isImprimirTermoPagDiferente"
            id="isImprimirTermoPagDiferente"
            innerRef={formProps.register}
          />
        </Col>
      </Row>

      <Row>
        <Col>
          <CustomInput
            type="switch"
            label="Exibir Notas Explicativas"
            name="exibirNotasExplicativas"
            id="exibirNotasExplicativas"
            innerRef={formProps.register}
          />
        </Col>
        <Col>
          <CustomInput
            type="switch"
            label="Imprimir a Distribuição de Lucros"
            name="imprimirDistribuicaoLucros"
            id="imprimirDistribuicaoLucros"
            innerRef={formProps.register}
          />
        </Col>
      </Row>

      <Row>
        <Col>
          <CustomInput
            type="switch"
            label="Alterar as Descrições do DRE"
            name="isAlterarDescricoes"
            id="idisAlterarDescricoes"
            innerRef={formProps.register}
          />
          {withTooltip(
            propToolTip('Caso essa opção seja selecionada as contas terão número de conta e código impressas antes do nome.'),
            'isAlterarDescricoes'
          )}
        </Col>
        <Col>
          <CustomInput
            type="switch"
            label="Trocar campo referencia do cabeçalho para data inicial/final"
            name="imprimirDataInicialFinal"
            id="imprimirDataInicialFinal"
            innerRef={formProps.register}
          />
        </Col>
      </Row>

      <Row className="mb-3">
        <Col>
          <CustomInput
            type="switch"
            disabled={formProps.getValues().modoSimplificado}
            onChange={enableTermoTwo}
            label="Imprimir Termos Separados"
            inline={true}
            name="isImprimirTermosSeparados"
            id="isImprimirTermosSeparados"
            innerRef={formProps.register}
          />
        </Col>
        {isEnabledIsMatriz && (
          <Col>
            <CustomInput type="switch" label="Acumular Matriz e Filiais" name="matrizFilial" id="matrizFilial" innerRef={formProps.register} />
          </Col>
        )}
      </Row>

      {formProps.getValues().isAlterarDescricoes && (
        <Card>
          <CardBody>
            <CardTitle>Descrições do DRE</CardTitle>
            <Row>
              <Col>
                <InputGroupInline
                  id="descricaoReceitaOperacional"
                  label="Receita Operacional Líquida"
                  isPrependLabel={true}
                  iconAppend={'reply'}
                  labelWidth={maxWidth}
                  appendClickHandler={getDescricaoReceitaOperacionalPadrao}
                  placeholder="digite a Descrição da Receita Operacional Líquida"
                  formProps={formProps}
                  tooltipProps={propToolTip('Altera a descrição exibida da Receita Operacional Líquida')}
                />
              </Col>
            </Row>
            <Row>
              <Col>
                <InputGroupInline
                  id="descricaoLucroBruto"
                  label="Lucro Bruto"
                  isPrependLabel={true}
                  iconAppend={'reply'}
                  labelWidth={maxWidth}
                  appendClickHandler={getDescricaoLucroBrutoPadrao}
                  placeholder="digite a Descrição do Lucro Bruto"
                  formProps={formProps}
                  tooltipProps={propToolTip('Altera a descrição exibida do Lucro Bruto')}
                />
              </Col>
            </Row>
            <Row>
              <Col>
                <InputGroupInline
                  id="descricaoResultadoOperacional"
                  label="Resultado Operacional Líquido"
                  isPrependLabel={true}
                  labelWidth={maxWidth}
                  iconAppend={'reply'}
                  appendClickHandler={getDescricaoResultadoOperacionalPadrao}
                  placeholder="digite a Descrição do Resultado Operacional Liquido"
                  formProps={formProps}
                  tooltipProps={propToolTip('Altera a descrição exibida do Resultado Operacional Líquido')}
                />
              </Col>
            </Row>
            <Row>
              <Col>
                <InputGroupInline
                  id="descricaoResultadoExercicio"
                  label="Resultado do Exercício"
                  isPrependLabel={true}
                  labelWidth={maxWidth}
                  iconAppend={'reply'}
                  appendClickHandler={getDescricaoResultadoExercicioPadrao}
                  placeholder="digite a Descrição do Resultado do Exércicio"
                  formProps={formProps}
                  tooltipProps={propToolTip('Altera a descrição exibida do Resultado do Exercício')}
                />
              </Col>
            </Row>
            <Row>
              <Col>
                <InputGroupInline
                  id="descricaoCSLL"
                  label="C.S.L.L"
                  isPrependLabel={true}
                  labelWidth={maxWidth}
                  appendClickHandler={getDescricaoCSLLPadrao}
                  iconAppend={'reply'}
                  placeholder="digite a Descrição do C.S.L.L"
                  formProps={formProps}
                  tooltipProps={propToolTip('Altera a descrição exibida da C.S.L.L')}
                />
              </Col>
            </Row>
            <Row>
              <Col>
                <InputGroupInline
                  id="descricaoImpostoRenda"
                  label="I.R"
                  isPrependLabel={true}
                  iconAppend={'reply'}
                  appendClickHandler={getDescricaoImpostoRendaPadrao}
                  labelWidth={maxWidth}
                  placeholder="digite a Descrição do Imposto de Renda"
                  formProps={formProps}
                  tooltipProps={propToolTip('Altera a descrição exibida do Imposto de Renda')}
                />
              </Col>
            </Row>
          </CardBody>
        </Card>
      )}

      {isEnabledInfoDiario && (
        <Row className="mt-3">
          <Col>
            <InputGroupInline
              id="livroDiario"
              label="Nº Livro Diário"
              isPrependLabel={true}
              formProps={{ ...formProps, validation: { required: isEnabledInfoDiario, max: 2147483647 } }}
              type={InputType.NUMBER}
              iconAppend={'reply'}
              appendClickHandler={getInfoDiario}
            />
          </Col>
          <Col>
            <InputGroupInline
              id="folhaInicialDiario"
              label="Folha Inicial Diário"
              isPrependLabel={true}
              formProps={{ ...formProps, validation: { required: isEnabledInfoDiario, max: 2147483647 } }}
              type={InputType.NUMBER}
            />
          </Col>
          <Col>
            <InputGroupInline
              id="folhaFinalDiario"
              label="Folha Final Diário"
              isPrependLabel={true}
              formProps={{ ...formProps, validation: { required: isEnabledInfoDiario, max: 2147483647 } }}
              type={InputType.NUMBER}
            />
          </Col>
        </Row>
      )}

      <hr className="mt-0" />

      <Row>
        <Col>
          <InputGroupInline
            id="dataEncerramento"
            label="Data de Encerramento"
            placeholder={'Informe a Data de Encerramento'}
            isPrependLabel={true}
            labelWidth={maxWidthTermo}
            formProps={formProps}
            type={InputType.DATE}
          />
        </Col>
      </Row>

      {isEnabledTermoOne && (
        <Row>
          <Col>
            <CustomizacaoTermoIS
              formProps={{ ...formProps, validation: { required: isEnabledTermoOne } }}
              label="Termo"
              searchViewProps={{ apenasDemonstracaoResultado: true }}
              apenasDemonstracaoResultado={true}
              placeholder="Selecione o termo"
              labelWidth={maxWidthTermo}
              id="termo"
              disabled={formProps.getValues().modoSimplificado}
              isPrependLabel={true}
              inputSearchProps={{ bsSize: 'xl' }}
              tooltipProps={propToolTip('Termo Demonstração de Resultado')}
            />
          </Col>
        </Row>
      )}

      {isEnabledTermoTwo && (
        <Row>
          <Col>
            <CustomizacaoTermoIS
              formProps={{ ...formProps, validation: { required: isEnabledTermoTwo } }}
              label="Segundo Termo"
              searchViewProps={{ apenasBalancoPatrimonial: true }}
              apenasBalancoPatrimonial={true}
              disabled={formProps.getValues().modoSimplificado}
              labelWidth={maxWidthTermo}
              placeholder="Selecione o segundo termo"
              id="termo2"
              isPrependLabel={true}
              inputSearchProps={{ bsSize: 'xl' }}
              tooltipProps={propToolTip('Termo Demonstração de Resultado')}
            />
          </Col>
        </Row>
      )}

      <hr className="mt-0" />

      <Row className="mt-3">
        <Col>
          <InputGroupInline
            id="consolidacao"
            label="Consolidação"
            isPrependLabel={true}
            placeholder="Selecione o tipo de consolidação"
            formProps={{ ...formProps, validation: { required: true } }}
            inputDropDownProps={{
              optionsProvider: CONSOLIDAR_ENUM_VALUES,
              selectProps: { isClearable: false },
            }}
            type={InputType.DROP_DOWN}
          />
        </Col>
      </Row>

      <Row>
        <Col>
          <ResponsavelIS
            id={'representante'}
            formProps={formProps}
            label={'Representante'}
            placeholder={'Informe os Representante'}
            isPrependLabel={true}
            inputSearchProps={{ bsbsSize: 'xl', selectProps: { isMulti: false } }}
          />
        </Col>
      </Row>

      <Row>
        <Col>
          <ResponsavelIS
            id={'outrosResponsaveis'}
            formProps={formProps}
            label={'Responsáveis adicionais para assinatura'}
            placeholder={'Informe os Responsáveis adicionais para a assinatura'}
            isPrependLabel={true}
            inputSearchProps={{ bsbsSize: 'xl', selectProps: { isMulti: true } }}
          />
        </Col>
      </Row>
      <ReportForm
        {...props}
        handleSubmit={handleSubmit}
        formProps={formProps}
        localStorageKey={CLASS_KEY}
        routeProps={props}
        reportBasicValues={DEFAULT_VALUES}
        reportViewOptions={DEFAULT_OPTIONS}
        validateExtras={validateExtras}
      />
    </WindowComponent>
  );
};

export default DemonstracaoResultadoAnualReportView;
